*{font-weight:normal}.approval{float:none;position:absolute;color:#76bcc2;font-size:10px;margin:-10px 0 0 0px}input{color:#153d39;font-size:1em}.dots{padding:.5em 0;border:1px dashed;border:1px dashed #76bcc2}.slider{float:none;padding:1.2em 0}#fader{width:60%;display:inline-block;vertical-align:middle}#fader2{width:70%;display:inline-block;vertical-align:middle}.increase,.decrease{clear:both;display:inline-block;color:#153d39;font-size:12px}.cars{padding:32px 0}.left{float:left;clear:right}.right{float:right;clear:right}.sidebar{max-width:262px;width:100%;height:330px;background-color:#fff;box-shadow:0 1px 3px 0 rgba(0,0,0,0.3);padding:16px;box-sizing:border-box;float:left;margin:0 32px 30px 15px}.sidebar h2{font-family:Helvetica;font-size:14px;line-height:1.3;color:#848c8b}.sidebar input{width:100%;height:40px;border-radius:2px;border:solid 1px #848c8b}.sidebar .sf{width:70%;float:left}.sidebar .tf{width:20%;margin-left:5%}.listing{max-width:698px;font-family:Helvetica;font-size:18px;line-height:1.4;color:#848c8b;display:inline-block;float:none;clear:both;margin:0 30px  0 15px}.listing h1{font-family:Helvetica;font-size:38px;color:#153d39;float:none;clear:both;margin:0;font-weight:normal}.listing .main-card{float:left;max-width:333px;width:100%;height:333px;background-color:#ffffff;background-color:#fff;box-shadow:0 1px 3px 0 rgba(0,0,0,0.3);display:inline-block;padding:17px;box-sizing:border-box;margin:0 0 32px 0}.listing .main-card:nth-child(odd){margin-right:32px}.listing .main-card img{float:none;clear:both;width:100%;max-width:250px;margin:6em auto;display:block}



